.intro { 
    text-align: center;
    min-height: 300px;
    background: #fbfafa;
}
.intro img {
    margin: 100px 0 50px 0;
    width: 95px;
    height: 95px;
}
.intro h1 {
    margin-top: 50px;
    font-size: 56px;
    font-family: 'Open Sans', sans-serif;
    color: #555559;
    font-weight: 300;
    max-width: 900px;
    margin: 0 auto;
}
.btn-intro-wrapper {
    margin: 100px 0 0 0;
    padding: 0 0 175px 0;
}
.btn-intro {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    height: 65px;
    width: 300px;
    margin: 0 auto;
    border-radius: 5px;
    font-size: 20px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    color: #ffffff;
    background: #286782;
    line-height: 1.2;
}
.hotdogs h2 {
    position: relative;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    color: #555559;
    margin: 0 30px 0 150px;
    font-weight: 600;
}
.hotdogs h2::after {
    position: absolute;
    top: 50%;
    left: -50px;
    content: '';
    height: 2px;
    width: 30px;
    background: #84959c;
}
.hotdogs p {
    margin: 40px 30px 0 150px;
    max-width: 450px;
    font-size: 18px;
    font-family: 'Open Sans', sans-serif;
    color: #555559;
    text-align: left;
}
.box-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
}
.box-wrapper:nth-child(2n) {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
}
.left-box, .right-box {
    width: 50%;
}
.left-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}
.right-box img {
    height: 100%;
    width: 100%;
}

@media (max-width: 1200px) {
    .intro img {
        height: 50px;
        width: 50px;
    }
    .intro h1 {
        font-size: 30px;
    }
    .btn-intro {
        height: 50px;
        width: 200px;
        font-size: 14px;
    }
}
@media (max-width: 1000px) {
    .hotdogs h2, .hotdogs p {
        margin-left: 70px;
    }
    .intro img {
        margin: 50px 0 30px 0;
    }
    .intro h1 {
        margin-top: 30px;
    }
    .intro .btn-intro-wrapper {
        margin-top: 50px;
        padding-bottom: 50px;
    }
}
@media (max-width: 900px) {
    .box-wrapper {
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .left-box,.right-box {
        width: 100%;
    }
    .right-box {
        margin: 20px;
    }
}

@media (max-width: 540px) {
    .hotdogs h2::after {
        display: none;
    }
    .hotdogs h2 {
        margin: 10px;
        text-align: center;
    }
    .left-box p {
        margin: 0 10px 0 10px;
        text-align: center;
    }
}
  