form {
    padding: 20px 0;
    width: 700px;
    margin: 50px auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
input,textarea {
    width: 500px;
    margin: 5px auto;
    outline: none;
    font-family: 'Open Sans', sans-serif;
    color: #286782;
    font-size: 25px;
    padding: 0 20px;
    border: 2px solid black;
}
input:focus {
    border: 2px solid #286782;
}
textarea:focus {
    border: 2px solid #286782;
}
input {
    height: 50px;
}
textarea {
    resize: none;
    height: 300px;
}
.sub {
    margin-top: 20px;
    width: 545px;
    color: #286782;
    cursor: pointer;
}
.sub:hover {
    background: #286782;
    color: white;
}

@media (max-width: 900px) {
    form {
        width: 620px;
    }
}
@media (max-width: 700px) {
    form {
        width: 400px;
    }
    input,textarea {
        width: 300px;
    }
    .sub {
        width: 345px;
    }
}
@media (max-width: 400px) {
    form {
        width: 270px;
    }
    input,textarea {
        width: 200px;
    }
    .sub {
        width: 245px;
    }
}