header {
    position: relative;
    z-index: 999;
    margin-top: 3px;
    -webkit-box-shadow: 0px 0px 3px 0.5px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0px 0px 3px 0.5px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 3px 0.5px rgba(0, 0, 0, 0.5);
}
.insta {
    position: absolute;
    top: 5px;
    right: 10px;
    z-index: 999;
}
.insta a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}   
.insta p {
    margin-left: 10px;
    color: white;
}
.hots-grid-wrapper {
    width: 100%;
}
.imgs-grid-1,
.imgs-grid-2 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    position: relative;
}
.imgs-grid-1 img,
.imgs-grid-2 img {
    margin: 0 -1px;
}
.shadow {
    position: absolute;
    top: -100px;
    width: 100%;
    height: 100px;
    -webkit-box-shadow: 0px 61px 35px -5px rgba(0, 0, 0, 0.7);
    -moz-box-shadow: 0px 61px 35px -5px rgba(0, 0, 0, 0.7);
    box-shadow: 0px 61px 35px -5px rgba(0, 0, 0, 0.7);
}
.menu {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 25px 0 25px 0;
}
.menu li {
    margin: 0 30px;
    font-size: 20px;
    text-transform: uppercase;
    padding: 10px;
    cursor: pointer;
}
.menu li:hover {
    background: #286782;
    color: white;
}
.menu li {
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    color: #2e2e2e;
}
#btn-menu {
    display: none;
}
.intro {
    text-align: center;
    min-height: 300px;
    background: #fbfafa;
}
  

@media (max-width: 1450px) {
    header img {
        max-height: 210px;
        max-width: 210px;
    }
}
@media (max-width: 1200px) {
    header .shadow {
        top: -60px;
        height: 40px;
    }
    header img {
        max-height: 157px;
        max-width: 157px;
    }
}

@media (max-width: 900px) {
    header .shadow {
        top: -60px;
        height: 40px;
    }
    header img {
        max-height: 114px;
        max-width: 114px;
    }
    header .menu li {
         margin: 0 10px;
    }
}
@media (max-width: 700px) {
    .hots-grid-wrapper {
        display: none;
    }
    nav {
        position: relative;
    }
    .menu {
        display: none;
        position: absolute;
        top: 100px;
        right: 0;
        left: 0;
        background: #286782;
    }
    .menu.active {
        display: block;
    }
    .menu li {
        text-align: center;
    }
    .menu li:hover {
        background: white;
        color: #286782;
    }
    #btn-menu {
        display: block;
        height: 100px;
        cursor: pointer;
        margin: 0 auto;
    }
}

  