footer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    border-style: 2px solid #e9e9e9;
    background-color: #efefef;
    min-height: 100px;
}
footer p, footer a {
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    color: #555559;
}
footer .rights {
    margin-left: 30px;
}
footer .contacts {
    margin-right: 30px;
}


@media (max-width: 1000px) {
    footer {
        -webkit-box-orient: vertical;
        -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
    }
    footer .rights,footer .contacts {
        margin: 0 30px;
        padding: 5px 0;
    }
    footer .rights a, footer .contacts a {
        margin: 5px 0;
    }
}


@media (max-width: 540px) {
    footer .contacts {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
        text-align: center;
    }
    footer .contacts span {
      display: none;
    }
}
  