* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}
a,button {
  cursor: pointer;
  border: none;
  outline: none;
}
.main {
  max-width: 1445px;
  margin: 0 auto;
}

@media (max-width: 1450px) {
  .main {
    max-width: 1200px;
  }
}
@media (max-width: 1200px) {
  .main {
    max-width: 900px;
  }
}

@media (max-width: 900px) {
  .main {
    max-width: 650px;
  }
}
